<template>
  <b-card
    tag="article"
     style="width: 20rem; height: 35rem;"
    class="mb-2"
  >
    <img
      :src="recipe.image"
      class="card-img-top recipe-preview"
      @click="goToRecipe(recipe.id)"
    >
    <b-card-body>
      <b-card-title>{{ recipe.title }}</b-card-title>
      <b-card-text>
        <p><img src="data:image/png;base64,"> {{ recipe.readyInMinutes }} minutes </p>
        <p><img src="data:image/png;base64,">{{ recipe.aggregateLikes }}</p>
        <div style="display: flex;">
          <p v-if="recipe.vegetarian" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
          <p v-if="recipe.vegan" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
          <p v-if="recipe.glutenFree" style="margin-right: 10px;"><img src="data:image/png;base64,"></p>
          <p v-if="!recipe.glutenFree"><img src="data:image/png;base64,"></p>
        </div>  
      </b-card-text>
      <!-- <b-button @click="goToRecipe(recipe.id)" variant="dark" class="mb-2">To The Recipe</b-button> -->
      <b-button variant="link" class="text-decoration-none mb-2" @click="toggleFav">
        <img v-if="!isFav" src="data:image/png;base64," alt="Image 1">
        <img v-else src="data:image/png;base64," alt="Image 2">
      </b-button>
      <img v-if="recipe.viewed" src="data:image/png;base64,">
  </b-card-body>
  <b-alert>
    <p>{{ message }}</p>
  </b-alert>
  </b-card>
</template>

<script>
import { mockAddFavorite } from "../services/user.js";
export default {
  name: 'RecipePriview',
  data() {
    return {
      image_load: false,
      isFav: localStorage.getItem(`favorite_${this.recipe.id}`) === 'true',
      message: ""
    };
  },
  props: {
    recipe: {
      type: Object,
      required: true
    },
  },
  methods: {
    goToRecipe(recipeId) {
      this.$router.push({name: 'recipe', params: { recipeId: recipeId }});
      this.recipe.viewed = true
  },
  toggleFav(event) {
      event.stopPropagation();
      this.isFav = !this.isFav;
      localStorage.setItem(`favorite_${this.recipe.id}`, this.isFav.toString());
      if (this.isFav){
        if (mockAddFavorite(this.recipe.id).status === 200){
          this.message = mockAddFavorite(this.recipe.id).response.data.message;
        }
      }
      else{
        this.message = "The Recipe successfully removed from favorites"
      }
      this.$root.toast("",this.message, "Light ");
    },
  }
};
</script>

<style scoped>
.recipe-preview {
  display: inline-block;
  width: 100%;
  cursor: pointer;
}
/* .recipe-preview:hover {
  filter: blur(1px);
} */
.recipe-preview:hover {
  transform: scale(1.05);
}
</style>
